.colaborations-container {
    margin: 1.5rem;
    width: 100%;
}

.colaborations-container .title {
    font-size: 1.3rem;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    margin-bottom: 1rem;
}

.colaborations-container .title p {
    margin-bottom: 0px;
}

.colaborations-container .info {
    display: flex;
}

.colaborations-container .columns {
    flex-grow: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.colaborations-container .column-container {
    display: flex;
}

.colaborations-container .column-actions {
    margin: 2rem;
}

.colaborations-container .column-actions Button {
    margin: 0rem .5rem;
}

.colaborations-container .form {
    flex-grow: 1;
}

.colaborations-container .colored-list {
    list-style-type: none;
    width: max-content;
}

.colaborations-container .colored-list li {
    background-color: #f7f7f7;
    padding: .2rem .5rem;
    margin: .75rem;
    text-align: center;
    font-size: 1.3rem;
    cursor: pointer;
}

.colaborations-container .colored-list li:hover {
    font-weight: bold;
}

.colaborations-container .colored-list li:nth-child(even) {
    color: #6cc6c6;
}

.colaborations-container .colored-list li:nth-child(odd) {
    color: #d8e994;
}

.colaborations-container .form {
    padding-left: 3rem;
    border-left: 1px #facb0b solid;
}