body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Main {
  background: #fff url(/web-test/static/media/main_background_light.0a052866.jpg) no-repeat center;
  background-size: cover;
  height: 90vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.Main {
    background: #fff url(/web-test/static/media/main_background_light.0a052866.jpg) no-repeat center;
    background-size: cover;
    height: 90vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding-bottom: 10rem;
}

.main-label {
    margin: 2rem;
    color: white;
    font-size: 2rem;
    font-weight: bold;
}
.About {
    min-height: 80vh;
    min-width: 100vw;
}

.About .columns {
    display: flex;
}

.column {
    flex-grow: 1;
    width: 100%;
}

.profile {
    width: 50%;
}

.columns p {
    font-size: 20px;
}
.Contact {
    min-width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
}

.column {
    flex-grow: 1;
}

.svg-inline--fa {
    padding: 5px;
}
.service-form {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.form-element {
    display: block;
}

.form-actions {
    display: flex;
    justify-content: flex-end;
    margin: 2rem;
}

.form-title {
    font-weight: bold;
    margin-bottom: .7rem;
    color: #cf5524;
}
.consultation-container {
    margin: 1.5rem;
    width: 100%;
}

.consultation-container .title {
    font-size: 1.3rem;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    margin-bottom: 1rem;
}

.consultation-container .title p {
    margin-bottom: 0px;
}

.consultation-container .info {
    display: flex;
}

.consultation-container .columns {
    flex-grow: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.consultation-container .column-container {
    display: flex;
}

.consultation-container .column-actions {
    margin: 2rem;
}

.consultation-container .column-actions Button {
    margin: 0rem .5rem;
}

.consultation-container .form {
    flex-grow: 1;
}

.consultation-container .colored-list {
    list-style-type: none;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.consultation-container .colored-list li {
    background-color: #f7f7f7;
    padding: .2rem .5rem;
    margin: .75rem;
    text-align: center;
    font-size: 1.3rem;
    cursor: pointer;
}

.consultation-container .colored-list li:hover {
    font-weight: bold;
}

.consultation-container .colored-list li:nth-child(even) {
    color: #6cc6c6;
}

.consultation-container .colored-list li:nth-child(odd) {
    color: #d8e994;
}

.consultation-container .form {
    padding-left: 3rem;
    border-left: 1px #facb0b solid;
}
